.student{
  width: 100%;
}
.nav{
  position:relative;
  height:150px;
  width:900px;
  margin:0px auto;
}
div.nav .title{
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 400px;
  height: 120px;
  text-align: center;
  border: 1px solid rgba(187, 187, 187, 1);
}
div.nav img{
  display:block;
  float:left;
  margin-top:10px;
  margin-left: 20px;
  height:100px;
  width:100px;
}
div.nav .title h1{
  position: relative;
  left:50px;
  top:20px;
  width: 300px;
  height: 50px;
  line-height: 52px;
  color: rgba(4, 110, 97, 1);
  font-size: 30px;
  margin:0;
}
div.nav .title h3{
  position: relative;
  left:50px;
  top:30px;
  width: 300px;
  height: 30px;
  line-height: 26px;
  color: rgba(4, 110, 97, 1);
  font-size: 20px;
  margin:0;
}
div.nav .navigationBar {
  position: absolute;
  bottom: 0px;
  left: 480px;
  margin: 0px;
  height:50px;
}
.navigationBar li{
  float: left;
  display: block;
  line-height: 50px;
  height:50px;
  color:#FFF;
  padding: 0 15px;
  font-size:24px;
  text-align:center;
  text-decoration:none;
  background-color:#33B7A4;
}
li.activeTag {
  background-color: #046E5F;
}
div.nav .exit{
  position: absolute;
  bottom: 0px;
  right: 10px;
}
div.nav  a{
  text-decoration:none;
  diplay:block;
  font-size:16px;
  height: 20px;
  line-height:20px;
  color: rgba(4, 110, 97, 1);
}
div.line{
  position: absolute;
  bottom: 0;
  length:0;
  width:900px;
  border: 1px solid rgba(187, 187, 187, 1)
}
.content{
  position: relative;
  width:900px;
  margin:0px auto;
}
.content h1{
  width: 141px;
  height: 41px;
  margin:10px;
  line-height: 41px;
  color: rgba(16, 16, 16, 1);
  font-size: 20px;
  text-align: center;
}
.content #selectForm {
  width:100%;
  height:50px;
  margin-top: 10px;
}
.select {
  float:left;
  height:30px;
  margin:10px 50px;
}
.select img{
  width:20px;
  height:20px;
  vertical-align: middle;
}
.submit input{
  width:50px;
  height:30px;
  font-size: 12px;
  background-color:#1E9205;
  color:#fff;
  border-radius: 5px;
}
table {
  font-size:18px;
  color:#333;
  border-width:1px;
  border-color:#eee;
  border-collapse:collapse;
  margin:20px auto;
}
table th {
  border-width:1px;
  padding:5px 10px;
  border-style:solid;
  border-color:#eee;
  background-color:#dedede;
}
table td{
  border-width: 1px;
  padding:5px 10px ;
  border-style: solid;
  border-color:#eee;
  background-color:#fff;
}
